<template>
    <home-container >
        <div class=' d-flex flex-column justify-content-end' style='min-height:90vh'>
            <div>

                <h2 >Sign In to your account</h2>
                <ion-text ><p class='mb-5'>Sign In with your email, username or <br>phone number</p></ion-text>

                <ion-label  position="stacked">User ID</ion-label>
                <ion-item  class="rounded border ion-margin-bottom" lines="none">
                    <ion-input placeholder="Enter Email, username or phone number here" v-model="user_id"></ion-input>
                    
                </ion-item>
                <ion-label  position="stacked">Password</ion-label>
                <ion-item  class="rounded border ion-margin-bottom" lines="none">
                    <ion-input placeholder="Enter password here" :type="showPassword ? 'text':'password'" v-model="password"></ion-input>
                    <ion-button @click.prevent="showPassword = !showPassword" color='dark' fill='clear' slot='end'>
                        <ion-icon v-if="!showPassword" slot="icon-only" :icon='eyeOffOutline'></ion-icon>
                        <ion-icon v-else slot="icon-only" :icon='eyeOutline'></ion-icon>
                    </ion-button>
                </ion-item>
                <div class='d-flex justify-content-end ion-margin-bottom'>
                    <ion-button fill='clear' size='small'>Forgot Password?</ion-button>
                </div>
                <ion-button strong :disabled="!user_id || !password || password.length < 4" @click="handleLogin" class="mt-4 " expand="block">Login</ion-button>
                <div class='d-flex flex-column align-items-center ion-margin-vertical'>
                    <span>New around here?</span>
                    <a href="" @click.prevent="router.push('sign-up')">Create Account</a>
                </div>
            </div>
        </div>
    </home-container>
    
</template>
<script>
import { defineComponent } from "vue";
import {

} from "@ionic/vue";
import {eyeOffOutline,eyeOutline } from "ionicons/icons";
import { useRouter } from "vue-router";
import { loadingController } from "@ionic/vue";

    export default defineComponent({
        components: { },
        data() {
            return {
                user_id:null,
                password:null,
                showPassword:false,
            }
        },
        setup() {
            const router = useRouter();
            return {
                router,eyeOffOutline,eyeOutline
            };
        },
        methods:{
            async handleLogin(){
                let loading = await loadingController.create({
                    message: "Please wait...",
                });
                loading.present();
                setTimeout(() => {
                    this.router.push('/home');
                    loading.dismiss();
                }, 1000);
            },
        }
});

</script>
